@import "../common/styles/variables";

.contactsBlock {
  background-color: $bgColor;
  padding: 50px 0;

  .contactsContainer {
    width: 70%;
    height: 100%;
    border: 1px solid;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    flex-wrap: wrap;
    box-sizing: border-box;
    padding: 20px 0;

    .formContainer {
      width: 80%;
      display: flex;
      flex-direction: column;
      margin: 0 auto;
      max-width: 400px;
      box-sizing: border-box;

      & > input, & > textarea {
        margin: 5px;
      }
    }

    .sendForm {
      margin: 15px;
      padding: 5px 10px;
      cursor: pointer;
      border-radius: 10px;
    }
  }
}







